import React from "react";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";

export default class PropositionFeatureForDevice extends React.Component {
    icon() {
        return this.props.proposition.features.getMobileDataDesc().icon || "wifi2"
    }

    findDescription = (bonusDescriptions, search = "data_desc") => {
        return (
            bonusDescriptions && bonusDescriptions.find(bonus => bonus.attribute === search)
        )
    };

    render() {
        let data_desc = this.findDescription(this.props.proposition.bonusDescriptions);
        data_desc = data_desc ? data_desc.value : null;
        return TooltipFromHtml.conditionalRenderWithClassName((!!data_desc?data_desc:"") + (!!this.props.proposition.features.getMobileRetentionDesc()?this.props.proposition.features.getMobileRetentionDesc():""), "");

    }
}